import { Injectable } from '@angular/core';
import { filter } from 'rxjs/operators';
import * as auth0 from 'auth0-js';
import { environment } from '../../../environments/environment';

import * as unirest from 'unirest';
import * as Promise from 'bluebird';

(window as any).global = window;

@Injectable()
export class AuthService {

  auth0 = new auth0.WebAuth({
    clientID: 'V5nVHV0bHZVyF2j6GFnONiBw2e5x6Amj',
    domain: 'newblt123.auth0.com',
    responseType: 'token id_token',
    audience: 'https://newblt123.auth0.com/userinfo',
    redirectUri: `${environment.authCallback}`,
    scope: 'openid'
  });

  constructor() {}

  public login(): void {
    this.auth0.authorize();
  }

  public handleAuthentication(): void {
    let that = this;
    this.auth0.parseHash((err, authResult) => {
      if (authResult && authResult.accessToken && authResult.idToken) {
        window.location.hash = '';
        this.setSession(authResult);
        that.auth(authResult.idToken).then(function(user) {
          console.log(user);
        });
      } else if (err) {
        console.log(err);
      }
    });
  }

  private setSession(authResult): void {
    // Set the time that the Access Token will expire at
    const expiresAt = JSON.stringify((authResult.expiresIn * 1000) + new Date().getTime());
    localStorage.setItem('access_token', authResult.accessToken);
    localStorage.setItem('id_token', authResult.idToken);
    localStorage.setItem('expires_at', expiresAt);
  }

  public logout(): void {
    // Remove tokens and expiry time from localStorage
    localStorage.removeItem('access_token');
    localStorage.removeItem('id_token');
    localStorage.removeItem('expires_at');
    // Go back to the home route
  }

  public isAuthenticated(): boolean {
    // Check whether the current time is past the
    // Access Token's expiry time
    const expiresAt = JSON.parse(localStorage.getItem('expires_at') || '{}');
    return new Date().getTime() < expiresAt;
  }

  private auth(token): Promise<any> {
    let that = this;
    let url = environment.apiAddress + `/api/v1/protected/auth`;

    let authorization = `Bearer ${token}`;

    return new Promise(function(resolve, reject) {
       unirest.get(url)
        .headers({'Accept': 'application/json', 'Authorization': authorization})
        .send()
        .end(function(resp) {
          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
    });
  }


}



