import { Pipe } from '@angular/core';
import { DecimalPipe } from '@angular/common';
import { FavoriteClip } from '../models/favorite-clip.model';
import * as moment from 'moment';

@Pipe({
  name: 'favoriteClipsSort'
})
export class FavoriteClipsPipe {
  transform(array: Array<FavoriteClip>): Array<FavoriteClip> {
    array.sort((a: FavoriteClip, b: FavoriteClip) => {
      let aMoment: moment.Moment = moment(a.createdAt, 'YYYY-MM-DD HH:mm:ss');
      let bMoment: moment.Moment = moment(b.createdAt, 'YYYY-MM-DD HH:mm:ss');

      return bMoment.diff(aMoment);
    });
    return array;
  }
}



