import * as Promise from 'bluebird';
import * as unirest from 'unirest';

import { Injectable } from '@angular/core';

import { environment } from '../../../environments/environment';
import { TrendingClips } from '../models/trending-clips';
import { LocalStorageService } from './localStorage.service';

interface Clip {}

@Injectable()
export class ClipsService {
  constructor(private localStorageService: LocalStorageService) {}

  getBySlug(slug: string): Promise<any> {
    let that = this;
    let url = environment.apiAddress + '/api/v1/clips/slug/' + slug;

    return new Promise(function(resolve, reject) {
       unirest.get(url)
        .headers({'Accept': 'application/json'})
        .send()
        .end(function(resp) {

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
    });
  }

  getFavorites(): Promise<any> {
    let that = this;

    let authorization = `Bearer ${this.localStorageService.getItem('id_token')}`;
    let url = environment.apiAddress + '/api/v1/protected/clips/favorites';

    return new Promise(function(resolve, reject) {
      unirest.get(url)
      .headers({'Accept': 'application/json', 'Authorization': authorization})
      .send()
      .end(function(resp) {

        if (resp && resp.error) {
          reject(resp.error);
          return;
        }

        if (resp && resp.body) {
          if (typeof resp.body === 'string') {
            resolve(JSON.parse(resp.body));
          } else {
            resolve(resp.body);
          }
        } else {
          reject('no response');
        }
      });
    });
  }

  getUserInfo(slug: string): Promise<any> {
    let authorization = `Bearer ${this.localStorageService.getItem('id_token')}`;
    let url = environment.apiAddress + `/api/v1/protected/clips/slug/${slug}/userinfo`;

    return new Promise(function(resolve, reject) {
      unirest.get(url)
      .headers({'Accept': 'application/json', 'Authorization': authorization})
      .send()
      .end(function(resp) {

        if (resp && resp.error) {
          reject(resp.error);
          return;
        }

        if (resp && resp.body) {
          if (typeof resp.body === 'string') {
            resolve(JSON.parse(resp.body));
          } else {
            resolve(resp.body);
          }
        } else {
          reject('no response');
        }
      });
    });
  }

  addFavorite(slug: string): Promise<any> {
    let authorization = `Bearer ${this.localStorageService.getItem('id_token')}`;
    let url = environment.apiAddress + `/api/v1/protected/clips/slug/${slug}/favorite`;

    return new Promise(function(resolve, reject) {
      unirest.post(url)
      .headers({'Accept': 'application/json', 'Authorization': authorization})
      .send()
      .end(function(resp) {

        if (resp && resp.error) {
          reject(resp.error);
          return;
        }

        if (resp && resp.body) {
          if (typeof resp.body === 'string') {
            resolve(JSON.parse(resp.body));
          } else {
            resolve(resp.body);
          }
        } else {
          reject('no response');
        }
      });
    });
  }

  removeFavorite(slug: string): Promise<any> {
    let authorization = `Bearer ${this.localStorageService.getItem('id_token')}`;
    let url = environment.apiAddress + `/api/v1/protected/clips/slug/${slug}/unfavorite`;

    return new Promise(function(resolve, reject) {
      unirest.post(url)
      .headers({'Accept': 'application/json', 'Authorization': authorization})
      .send()
      .end(function(resp) {

        if (resp && resp.error) {
          reject(resp.error);
          return;
        }

        if (resp && resp.body) {
          if (typeof resp.body === 'string') {
            resolve(JSON.parse(resp.body));
          } else {
            resolve(resp.body);
          }
        } else {
          reject('no response');
        }
      });
    });
  }

  getTrendingInitial(period: string, language: string): Promise<TrendingClips> {
    return this.getTrending(undefined, period, language);
  }

  getTrending(cursor: string|undefined, period: string, language: string): Promise<TrendingClips> {
    let clips: Array<Clip> = new Array<Clip>();
    let that = this;

    return new Promise(function(resolve, reject) {
      let url = environment.apiAddress + `/api/v1/clips/trending?period=${period}&language=${language}`;

      if (cursor) {
        url += `?cursor=${cursor}`;
      }

      unirest.get(url)
        .headers({'Accept': 'application/json'})
        .send()
        .end(function(resp) {

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
    });
  }

  getTrendingByChannel(channel: string, cursor: string|undefined, period: string, language: string): Promise<TrendingClips> {
    let clips: Array<Clip> = new Array<Clip>();
    let that = this;

    return new Promise(function(resolve, reject) {
      let url = environment.apiAddress + `/api/v1/clips/trending/channel/${channel}?period=${period}&language=${language}`;

      if (cursor) {
        url += `?cursor=${cursor}`;
      }

      unirest.get(url)
        .headers({'Accept': 'application/json'})
        .send()
        .end(function(resp) {

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
    });
  }

  getTrendingByGame(game: string, cursor: string|undefined, period: string, language: string): Promise<TrendingClips> {
    let clips: Array<Clip> = new Array<Clip>();
    let that = this;

    return new Promise(function(resolve, reject) {
      let url = environment.apiAddress + `/api/v1/clips/trending/game/${game}?period=${period}&language=${language}`;

      if (cursor) {
        url += `?cursor=${cursor}`;
      }

      unirest.get(url)
        .headers({'Accept': 'application/json'})
        .send()
        .end(function(resp) {

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
    });
  }
}

export default new ClipsService(new LocalStorageService());



